import { getUrlParam } from "@/assets/js/common";
import { orderExpressInfo } from "@/servers/api";

export default {
  components: {},
  data() {
    return {
      expressData: []
    };
  },
  mounted() {
    this.getExpressInfo();
  },
  methods: {
    getExpressInfo() {
      orderExpressInfo({
        // g_order: getUrlParam(this),
        g_order: "SH2018110814292951118"
      })
        .then(res => {
          console.log(res.data.data.data);
          if (res.result === 1) {
            this.expressData = res.data.data.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getParmas() {
      getUrlParam();
    }
  }
};



